import InputComponent from "@/components/common/InputComponent";
import DividerXComponent from "@/components/common/DividerXComponent";
import SubtitleComponent from '@/components/common/SubtitleComponent';

export default function PriceEstimation() {
    return (
        <div className='w-full flex flex-row justify-center bg-[#FFFBF2]'>
            <div className='lg:w-[1200px] md:w-[864px] sm:w-[608px] xs:w-[280px] lg:px-20 md:px-0 sm:px-0'>
                <div className='grid grid-cols-1 grid-flow-row'>
                    <div className='flex flex-row justify-between items-center pt-14'>
                        <SubtitleComponent className='uppercase text-primary' subTitle='Estimasi Harga' />
                    </div>
                    <form className="flex flex-col gap-6 pt-10">
                        <DividerXComponent />
                        <InputComponent props={{ id: 'name', label: 'Nama', placeHolder: 'Masukkan Nama Lengkap Anda' }} />
                        <InputComponent props={{ id: 'phone', label: 'Nomor HP / WhatsApp', placeHolder: '0812 3456 7890' }} />
                        <InputComponent props={{ id: 'email', label: 'Email', placeHolder: 'emailanda@gmail.com' }} />
                        <InputComponent props={{ id: 'type', label: 'Jenis', placeHolder: 'Café / Restoran / Coffee Shop / dll' }} />
                        <InputComponent props={{ id: 'size', label: 'Ukuran', placeHolder: 'Dalam satuan m2' }} />
                        <InputComponent props={{ id: 'location', label: 'Lokasi', placeHolder: 'Nama Kota' }} />
                        <InputComponent props={{ id: 'job', label: 'Pengerjaan', placeHolder: 'Konstruksi atau Renovasi' }} />
                        <InputComponent props={{ id: 'time', label: 'Estimasi Waktu', placeHolder: '3 Bulan' }} />
                        <InputComponent props={{ id: 'additional_info', label: 'Informasi Tambahan', isTextArea: true, placeHolder: 'Untuk mempercepat proses estimasi harga, mohon berikan rincian deskripsi proyek Anda secara detail dan lengkap. Terima kasih.' }} />
                        <div className="flex flex-row justify-between">
                            <button className="border-2 border-[#707070] text-center my-6 py-6 px-10 text-partial font-dm-sans font-bold lg:text-xl text-base">Kirim Pesan</button>
                        </div>
                        <DividerXComponent />
                    </form>
                </div>
            </div>
        </div>

    )
}
